
#app {
}

#login{
    margin: 20% auto;
}
.loading{
    width: 100px;
}
.modal-login{
    -webkit-box-shadow: none!important;
            box-shadow: none!important;
    margin-top: 10%!important;
    background: #fff0!important;
}
.txt-white{
    color: white;
}
.modal-bg{
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 5;
    background: #2185d0;
}
.content label{
    font-weight: lighter!important;
    position: absolute;
    /* top: 10px; */
    left: 15px;
    z-index: -1;
    -webkit-transition: .15s all ease-in-out;
    transition: .15s all ease-in-out;
}
.ui.input{
    width: 100%;
    margin: 10px auto;
}

/* Fade Transition Animation */
.fade-enter-active, .fade-leave-active {
    -webkit-transition: opacity .5s;
    transition: opacity .5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
    opacity: 0;
}
*::-webkit-input-placeholder {
    color: red;
}
*:-moz-placeholder {
    /* FF 4-18 */
    color: red;
}
*::-moz-placeholder {
    /* FF 19+ */
    color: red;
}
*:-ms-input-placeholder {
    /* IE 10+ */
    color: red;
}




































.user-menu-container{
    text-align: center;
}
.side-menu-content{
    float: left;
}
.side-menu-user{
    padding: 20px;
    color: white;
}
span.username{
    margin-left: 5px;
}
.user-menu{
    text-align: center;
}
.user-link{
    padding-left: 20px;
    padding-right: 20px;
}
.user-link:hover{
    cursor: pointer;
}
.user-icon{
    -webkit-transition: -webkit-transform .2s;
    transition: -webkit-transform .2s;
    transition: transform .2s;
    transition: transform .2s, -webkit-transform .2s;
}
.user-icon:hover{
    cursor: pointer; /* IE 9 */
    -webkit-transform: scale(1.2); /* Safari 3-8 */
    transform: scale(1.2);
}
.sm-bgcolor-blue{
    background: #2185d0 !important;
}
.sm-color-white{
    color: white !important;
}

